.hero {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-style: oblique;
    overflow: hidden;
  }
  
  .hero h1 {
    font-size: 3rem;
    font-weight: bold;
    font-style: oblique;
  }
  
  .highlight {
    color: rgb(225, 69, 69);
  }
  
  .hero p {
    font-size: 1.5rem;
    margin-top: 10px;
  }
  
  .hero-btn {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1.2rem;
    background-color: rgb(117, 28, 28);
    color: white;
    border-radius: 5px;
    transition: 0.3s;
  }
  